import { HardwiredSourceObject } from "../types";

/**
 * Transforms source to HardwiredSourceObject.
 * @param source Function (call to return HardwiredSourceObject), string (code, without dependencies), object (HardwiredSourceObject)
 */
export const useSource = (
  source: Function | string | object
): HardwiredSourceObject | null => {
  switch (typeof source) {
    case "function":
      return source();
    case "string":
      return { code: source + "" };
    case "object":
      const newSource = source as HardwiredSourceObject;
      return newSource ? newSource : null;
  }
  return null;
};
